import React, { ReactDOM } from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Form,
  FormGroup
} from "reactstrap";
import axios from "axios";

class ModalComponenent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      description: "",
      endDate: "",
      modal: this.props.show,
      backdrop: true,
      buttonDisabled: true,
      error: {
        error: false,
        errMessage: ""
      }
    };

    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }

  submitHandler = () => {
    console.log(this.state);
    let headers = {
      "Content-Type": "application/json",
      authorization: localStorage.getItem("jwt")
    };
    let url = "http://localhost:3001/api/tasks/newTask";
    let data = {
      title: this.state.title,
      description: this.state.description,
      endDate: this.state.endDate
    };
    axios
      .post(url, data, { headers: headers })
      .then(data => {
        console.log();
        if (data.data.success) {
          this.toggle();
          this.props.updatePage();
        } else {
            this.setState({error:{error:true, errMessage:"Something went wrong. Please check the input once again"}})
        }
      })
      .catch(err => {
        console.log(err.response);
      });
  };

  disableButton = () => {
    if (
      this.state.description ||
      this.state.title ||
      this.state.endDate == ""
    ) {
      this.setState({ buttonDisabled: false });
    }
  };
  render() {
    const errorComponent = this.state.error.error ? (
      <p>{this.state.error.errMessage}</p>
    ) : null;
    return (
      <div>
        <Modal
          isOpen={this.state.modal}
          toggle={this.toggle}
          className={this.props.class}
          backdrop={this.state.backdrop}
        >
          <ModalHeader toggle={this.toggle}>
            <h3>Create a new Task</h3>
          </ModalHeader>
          <ModalBody>
            <form>
              <div className="form-group row">
                <label
                  htmlFor="staticEmail"
                  className="col-sm-3 col-form-label"
                >
                  Title
                </label>
                <div className="col-sm-9">
                  <input
                    placeholder="Give your task a name"
                    autoComplete="off"
                    type="text"
                    className="form-control"
                    value={this.state.title}
                    onChange={e => {
                      this.setState({ title: e.target.value });
                      this.disableButton();
                      console.log(this.state);
                    }}
                  />
                </div>
                <label
                  htmlFor="staticEmail"
                  className="col-sm-3 col-form-label"
                >
                  Description
                </label>
                <div className="col-sm-9">
                  <textarea
                    autoComplete="off"
                    className="form-control"
                    placeholder="Enter the Description here"
                    onChange={e => {
                      this.setState({ description: e.target.value });
                      console.log(this.state);
                    }}
                  />
                </div>

                <label
                  htmlFor="staticEmail"
                  className="col-sm-3 col-form-label"
                >
                  EndDate
                </label>
                <div className="col-sm-9">
                  <input
                    type="date"
                    className="form-control"
                    onChange={e => {
                      this.setState({ endDate: e.target.value });
                      console.log(this.state);
                    }}
                  />
                </div>
              </div>
            </form>
          </ModalBody>

          <ModalFooter>
            {errorComponent}
            <Button
              color="primary"
              onClick={this.submitHandler}
              disabled={this.state.buttonDisabled}
            >
              Create
            </Button>
            <Button color="danger" onClick={this.toggle}>
              Cancel
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default ModalComponenent;
