import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";

class SignUpComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      username: "",
      error: {
        error: false,
        errorMessage: ""
      },
      success:{
        success:false,
        succceMessage:"" 
      }
    };
  }
  handleSubmit = event => {
    event.preventDefault();
    axios({
      method: "POST",
      url: "https://nameless-headland-34291.herokuapp.com/api/auth/register",
      data: {
        email: this.state.email,
        password: this.state.password,
        username: this.state.username
      },
      config: { headers: { "Content-Type": "application/json" } }
    })
      .then(data => {
        console.log(data);
        if (data.data.success == false) {
          this.setState({
            error: { error: true, errorMessage: "User Already Exists" }
          });
          console.log(this.state);
        }
        else{
          this.setState({
            success:{success:true, succceMessage:"Successfully Authenticated..Redirecting to login"}
          });
        }
        setTimeout(()=>{
          this.props.history.push('/signin');
        },3000);
      })
      .catch(err => {
        console.log(err.response);
      });
  };
  
  render() {
    return (
      <div>
        <div className="login-clean">
          <form onSubmit={this.handleSubmit}>
            <h2 className="sr-only">Login Form</h2>
            <div className="illustration">
              <h1>Sign Up</h1>
              <i className="icon ion-ios-football" />
            </div>
            <div className="form-group">
              <input
                autoComplete="off"
                className="form-control"
                type="email"
                name="email"
                placeholder="Email"
                onChange={e => {
                  this.setState({ email: e.target.value });
                }}
              />
            </div>
            <div className="form-group">
              <input
                autoComplete="off"
                className="form-control"
                type="text"
                placeholder="Username"
                onChange={e => {
                  this.setState({ username: e.target.value });
                }}
              />
            </div>
            <div className="form-group">
              <input
                autoComplete="off"
                className="form-control"
                type="password"
                name="password"
                placeholder="Password"
                onChange={e => {
                  this.setState({ password: e.target.value });
                }}
              />
            </div>
            {
              this.state.error.error?<p>{this.state.error.errorMessage}</p>:null
            }
            {
              this.state.success.success?<p>{this.state.success.succceMessage}</p>:null
            }
            <div className="form-group">
              <button className="btn btn-primary btn-block" type="submit">
                Sign Up
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(SignUpComponent);
