import React, { Component } from "react";
import Task from "./Todo/task";
import Moment from "react-moment";
import axios from "axios";
import { Modal, ModalBody, ModalHeader } from "reactstrap";
import ModalComponent from "./Todo/Modals";
import DatePicker from "react-datepicker";

export default class HomeComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modalShow: false,
      tasks: []
    };
  }

  createTask = () => {
    this.setState({ modalShow: !this.state.modalShow });
    console.log(this.state.modalShow);
  };

  componentDidMount() {
    let headers = {
      "Content-Type": "application/json",
      authorization: localStorage.getItem("jwt")
    };
    axios
      .get("http://localhost:3001/api/tasks/", { headers: headers })
      .then(data => {
        console.log(data);
        this.setState({ tasks: data.data.data });
      })
      .catch(err => {
        console.log(err.response);
      });
  }
  updatePage = () => {
    console.log("Page is updating");
    let headers = {
      "Content-Type": "application/json",
      authorization: localStorage.getItem("jwt")
    };
    axios
      .get("http://localhost:3001/api/tasks/", { headers: headers })
      .then(data => {
        console.log(data.data);
        this.setState({ tasks: data.data.data });
      })
      .catch(err => {
        console.log(err.response);
      });
  };

  render() {

    const emptyTasks=this.state.tasks.length==0?<p>You have no tasks. Go ahead and create one</p>:null;
    const modal = this.state.modalShow ? (
      <ModalComponent show={true} updatePage={this.updatePage} />
    ) : null;
    return (
      <div>
        {modal}
        <div className="container" id="startContainer">
          <div className="row">
            <div className="col-10">
              <h1 className="d-flex flex-row justify-content-between">
                My Tasks
                <button
                  className="btn btn-primary"
                  type="button"
                  onClick={this.createTask}
                >
                  Create New Task
                </button>
                <h2>Days Left</h2>
              </h1>
            </div>
          </div>

          <div className="contentdiv">
            <div className="row">
              <div className="col-10">
                <ul className="list-group">
                {emptyTasks}
                  {this.state.tasks.map(task => {
                    return (
                      <Task
                        key={task._id}
                        completed={task.completed}
                        updatePage={() => this.updatePage()}
                        title={task.title}
                        description={task.description}
                        endDate={task.endDate}
                        id={task._id}
                        createdDate={task.createdDate}
                      />
                    );
                  })}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
