import React, { Component } from "react";
import axios from "axios";
import { withRouter } from "react-router-dom";

class LoginComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      error:{
        error:false,
        errorMessage:''
      }
    };
  }

  //Method for submitting sign in form details
  handleSubmit = event => {
    event.preventDefault();
    axios({
      method: "POST",
      url: "https://nameless-headland-34291.herokuapp.com/api/auth/login",
      data: { email: this.state.email, password: this.state.password },
      config: { headers: { "Content-Type": "application/json" } }
    })
      .then(data => {
        localStorage.setItem("jwt", data.data.token);
        setTimeout(()=>{
          this.props.history.push('/');
        },3000);
      })
      .catch(err => {
        this.setState({
          error:{error:true, errorMessage:err.response.data.msg}
        });
        console.log(this.state);
        console.log(err.response);
      });
  };

  render() {

    const errComponent=this.state.error.error?<p>{this.state.error.errorMessage}</p>:null;
    return (
      <div>
        <div className="login-clean">
          <form onSubmit={this.handleSubmit}>
            <h2 className="sr-only">Login Form</h2>
            <div className="illustration">
              <h1>Login</h1>
              <i className="icon ion-ios-football" />
            </div>
            <div className="form-group">
              <input
                autoComplete="off"
                className="form-control"
                type="email"
                name="email"
                placeholder="Email"
                onChange={e => this.setState({ email: e.target.value })}
                value={this.state.email}
              />
            </div>
            <div className="form-group">
              <input
                onChange={e => this.setState({ password: e.target.value })}
                autoComplete="off"
                className="form-control"
                type="password"
                name="password"
                placeholder="Password"
                value={this.state.password}
              />
            </div>
            {errComponent}
            <div className="form-group">
              <button className="btn btn-primary btn-block" type="submit">
                Log In
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(LoginComponent);
