import React, { Component } from "react";
import Moment from "react-moment";
import { Collapse, Button, CardHeader, CardBody, Card } from "reactstrap";
import axios from 'axios';
class Task extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapse: false
    };
  }
  toggle = () => {
    console.log(this.props);
    this.setState({
      collapse: !this.state.collapse
    });
  };


  deleteTask = () => {
    console.log("Deleting the task with id ", this.props);
    let headers={
        'Content-Type':'application/json',
        'authorization':localStorage.getItem('jwt'),
        'id':this.props.id
    }
    axios.delete('http://localhost:3001/api/tasks/delete',{headers:headers}).then(data=>{
        console.log(data.data.data);
        this.setState({tasks:data.data.data});
        this.props.updatePage()
    }).catch(err=>{
        console.log(err.response);
    });
  };
  render() {
    const backgroundColor = this.state.collapse ? "#3bb300" : "#ffffff";
    const remaining = (
    //   <Moment duration={Date.now()} date={this.props.endDate} />
    <Moment fromNow>{this.props.endDate}</Moment>        
    );
    const created=(
        <Moment fromNow ago>{this.props.createdDate}</Moment>
    )
    return (
      <div>
        <Collapse isOpen={this.state.collapse}>
          <Card>
            <CardHeader onClick={this.toggle}>
              <h3>{this.props.title}</h3>
            </CardHeader>
            <CardBody>
              <p>Description:{this.props.description}</p>
              <p>Task ends {remaining}</p>
              <p>Created Date:{created}</p>
              <Button color="primary" onClick={this.deleteTask}>
                Delete Task
              </Button>
            </CardBody>
          </Card>
        </Collapse>

        <div>
          <li
            className="list-group-item d-flex flex-row justify-content-between"
            onClick={this.toggle}
          >
            <span>{this.props.title}</span>
            <span className="badge badge-primary">{remaining}</span>
          </li>
        </div>
      </div>
    );
  }
}

export default Task;
